<template>
	<div class="up-coming">
		<ul @touchmove="moveflag=true">
			<li class="item" v-for="item in movieList" :key="item.filmId" @touchend="handleEnterMovieDetail(item.filmId)">
				<div class="item-img">
					<img :src="item.poster">
				</div>
				<div class="item-info">
					<h3 class="title">{{item.name}}</h3>
					<p class="actor">主演:
						<span v-for="(val, i) in item.actors" :key="i">
							{{val.name}}
						</span>
					</p>
					<p class="run-time">上映时间：{{item.premiereAt | setTime }}</p>
				</div>
				<!-- <p>影片详情：{{item.synopsis}}</p> -->
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'upcoming',
	data() {
		return {
			movieList: [],
			moveflag: false
		}
	},
	activated() {
		this.getUpComingMovieList();
	},
	methods: {
		getUpComingMovieList() {
			this.axios({
				url: "https://m.maizuo.com/gateway?cityId=440100&pageNum=1&pageSize=10&type=2&k=3308357",
				headers: {
					"X-Client-Info": '{"a":"3000","ch":"1002","v":"5.0.4","e":"1583716737571230650801","bc":"440100"}',
					"X-Host": 'mall.film-ticket.film.list'
				}
			}).then(res => {
				// console.log(res.data)
                this.movieList = res.data.data.films;
			})
		},
		handleEnterMovieDetail(movieId) {
			if(this.moveflag) {
				this.moveflag = false
				return;
			}
			// console.log(this.moveflag)
			this.$router.push("filmDetail/coming/" + movieId)
		}
	},
	filters: {
		setTime(data) {
			data = data.toString().padEnd(13, "0")
			let d = new Date(Number(data));
			let day = ["日", "一", "二", "三", "四","五", "六"].filter((item, i) => i === d.getDay())
			return `周${day} ${d.getMonth()+1}月${d.getDate()}日`;
		}
	},
}
</script>

<style scoped>
.up-coming {
	background-color: #d2e2ca;
}
.up-coming ul {
	padding-left: 15px;
	border-top: 1px solid #679ce2;
}
.up-coming ul li {
	position: relative;
	display:flex;
	flex-direction:row;
	justify-content:flex-start;
	align-items:center;
	width: 100%;
	height: 94px;
	padding: 15px 15px 15px 0;
	border-top: 1px solid red;
}
.up-coming ul li:first-of-type {
	border-top: 0;
}
.up-coming .item-img{
	position: absolute;
	display: flex;
	align-items: center;
	width: 66px;
	height: 94px;
	background: rgb(210, 226, 202);
}
.up-coming .item-img>img {
	width: 100%;
}
/* calc(100% - 10px) 函数用于动态计算长度值  + - * / */
.up-coming .item-info{
	position: absolute;
	left: 60px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: flex-start;
	width: calc(100% - 120px);
	padding: 0 20px;
}
.up-coming .item-info .title,
.up-coming .item-info .actor,
.up-coming .item-info .run-time {
	width: 100%;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.up-coming .item-info .title{
	margin-bottom: 10px;
	font-size: 18px;
}
</style>
